export const ConvertModeFunctional = (text) => {
    return text.replaceAll("const", "function")
        .replaceAll("= () =>", "(props){ \n return ( \n")
        .replaceAll(">;", "> \n }")
}
export const ConvertModeArrowFunction = (text) => {
    return text.replaceAll("= () =>", "= (props) => { \n return ( \n")
        .replaceAll(">;", "> \n   ) \n}")
}