import React from 'react'

const CheckBox = ({ title, value,checked,onClick }) => {
    return (
        <div  onClick={onClick} className="c-checkbox c-checkbox--svg">
            <input checked={checked} value={value} className="sr-only" id={value} type="checkbox" />
            <label className="c-checkbox__label c-checkbox__label--svg" for={value}>
                <svg viewBox="0 0 100 100" className="icon-svg checkmark-icon" role="presentation">
                    <path className="checkbox-icon-svg-path" fill="none" stroke="#000" stroke-width="14" stroke-linecap="square" stroke-linejoin="square" stroke-miterlimit="10" d="M12.1 52.1l24.4 24.4 53-53" />
                </svg>{title}</label>
        </div>
    )
}

export default CheckBox
