import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import { NavLink } from 'react-router-dom'

const Header = () => {
    const [activeRoute, setActiveRoute] = useState("");
    const [showMore, setShowMore] = useState(false);
    const handleRouteChange = (route) => {
        setActiveRoute(route.target.innerHTML);
        setShowMore(false)
    }
    const isMobile = window.innerWidth < 900;

    useEffect(() => {
        setActiveRoute(window.location.pathname.replace("/", ""))

    }, [])
    return (
        <div className='header' >
            <nav className="flexSB">
                <ul className="flexSB ">
                    <li className='hamberger' >
                        {(isMobile && !showMore) && <span onClick={() => setShowMore(true)} >☰</span>}
                        <NavLink onClick={(e) => handleRouteChange(e)} to="/">Courses</NavLink></li>
                    {(isMobile && showMore) &&
                        <>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/json">Json</NavLink></li>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/javascript">Javascript</NavLink></li>
                            <li><a href="/javascript#Regex">Regex</a></li>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/reactjs">Reactjs</NavLink></li>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/convertHtmlToReact">Convert Html to React</NavLink></li>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/nodejs">Nodejs</NavLink></li>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/journal">Django</NavLink></li>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/contact">Angular</NavLink></li>
                            <li><NavLink onClick={(e) => handleRouteChange(e)} to="/courses">Purchase</NavLink></li>
                        </>}
                    {!isMobile && <>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/json">Json</NavLink></li>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/javascript">Javascript</NavLink></li>
                        <li><a href="/javascript#Regex">Regex</a></li>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/reactjs">Reactjs</NavLink></li>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/convertHtmlToReact">Convert Html to React</NavLink></li>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/nodejs">Nodejs</NavLink></li>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/journal">Django</NavLink></li>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/contact">Angular</NavLink></li>
                        <li><NavLink onClick={(e) => handleRouteChange(e)} to="/courses">Purchase</NavLink></li>

                    </>}
                </ul>
                <div className="start">
                    <div className="button">{activeRoute}</div>
                </div>
            </nav>
        </div >
    )
}

export default Header
