/* eslint-disable */
import CheckBox from '../../components/checkbox';
import TextareaWithLabel from '../../components/textareaWithLabel'
import { objToString, propsToString } from '../../helpers/objectToString'
import { mapObject } from '../../helpers/mapObject'
import { htmlToElement } from '../../helpers/textToHtml'
import { ConvertModeFunctional, ConvertModeArrowFunction } from '../../helpers/reactjsConvertMode'
import React, { useEffect, useState } from 'react'
const extractReactComponents = require("html-to-react-components");

const index = () => {
    const [HtmlText, setHtmlText] = useState(`<div data-component="Main" >
    <div data-component="Input" class="awesome" style="border: 1px solid red">
      <label for="name">Enter your name: </label>
      <input type="text" id="name" />
    </div>
    <div data-component="Input" class="awesome" style="border: 1px solid red">
      <label for="name">Password: </label>
      <input type="text" id="password" />
    </div>
    <p data-component="Info" >Enter your HTML here</p>
    </div>`);
    const [Components, setComponents] = useState({});
    const [Reactjs, setReactjs] = useState('');
    const [typeOfConvert, setTypeOfConvert] = useState('stateless');
    const convertHtmlToReact = () => {
        const HtmlItem = (mode) => extractReactComponents(
            HtmlText,
            {
                componentType: mode,
                moduleType: false,
            });
        if (typeOfConvert === 'functional') {
            setReactjs(ConvertModeFunctional(objToString(HtmlItem('stateless'))))
            setComponents(HtmlItem('stateless'))

        } else if (typeOfConvert === "stateless") {
            setReactjs(ConvertModeArrowFunction(objToString(HtmlItem('stateless'))))
            setComponents(HtmlItem('stateless'))
        }
        else {
            setReactjs(objToString(HtmlItem(typeOfConvert)))
            setComponents(HtmlItem(typeOfConvert))
        }
    }
    const handleChangeType = (e) => {
        setTypeOfConvert(e)

    }
    useEffect(() => {
        convertHtmlToReact()
    }, []);
    const typeMode = typeOfConvert === 'functional' ? ConvertModeFunctional : "stateless" ? ConvertModeArrowFunction : () => { };
    
    return (
        <div>
            <div>
                <div className='convert-info' >use data-component for each Tag You want to create componnet</div>
                <div className='container' >
                    <div className='mt-10 gap-9 flex flex-wrap' >
                        <CheckBox onClick={(e) => handleChangeType(e.target.value)} checked={typeOfConvert === "class"} title={"classBase"} value={"class"} />
                        <CheckBox onClick={(e) => handleChangeType(e.target.value)} checked={typeOfConvert === "stateless"} title={"ArrowFunction"} value={"stateless"} />
                        <CheckBox onClick={(e) => handleChangeType(e.target.value)} checked={typeOfConvert === "functional"} title={"functional"} value={"functional"} />
                        <CheckBox onClick={(e) => handleChangeType(e.target.value)} checked={typeOfConvert === "es5"} title={"es5"} value={"es5"} />
                    </div>
                </div>
                <div className={" wrapper"} >
                    <TextareaWithLabel onChange={(value) => setHtmlText(value)} value={HtmlText} />
                    <button onClick={convertHtmlToReact} className='convertButton' >Convert
                    </button>
                </div>
            </div>
            {HtmlText.length > 11 &&
            <div className="h3-wrap-list container">
                {mapObject(Components).length > 0 && mapObject(Components).map((item, i) => {
                    const ReactElement = typeMode(item?.value);
                    const children = htmlToElement(HtmlText)?.children;
                    const components = children[i] && children[i]?.dataset?.component;
                    let attributes = children[i] && children[i]?.children[i]?.attributes;

                    let props = attributes && mapObject(attributes)?.map((item, i) => { return { component: components, name: item?.value.nodeName, value: item?.value.nodeValue } })
                    props?.push({ component: components, name: 'children', value: "test" })
                    const ElementWithProps = props && props[i]?.component == item?.name ? ReactElement.replace(`return ( `, `const {${propsToString(props)}} = props; \n return ( `) : ReactElement
                    let ReactjsCode = ElementWithProps;
                    props && props.map((item, i) => {
                        let regex = new RegExp(`${item?.name}="${item?.value}"`, "g");
                        let matches = ReactjsCode.match(regex);
                        let newStr = ReactjsCode;
                        if (matches) {
                            for (let match of matches) {
                                newStr = newStr.replace(match, `${item?.name}={${item?.name}}`);
                            }

                        }
                        ReactjsCode = newStr;
                    })
                    children && mapObject(children)?.map((item, i) => {
                        let newStr = ReactjsCode;
                        newStr = newStr.replaceAll(item.value.innerText, "{children}");
                        ReactjsCode = newStr;
                    })
                   

                    return (
                        <div className="h3-wrap">
                            <h3 id={item?.name}><a className="h-anchor" href={item?.name}></a>{item?.name}</h3>
                            <div className="section">
                                <pre style={{ position: 'relative' }}>
                                    {ReactjsCode}
                                </pre>
                            </div>
                        </div>
                    )
                })}
            </div>
            }
        </div>
    )
}

export default index
