import React, { useEffect, useState } from 'react'

const TextareaWithLabel = ({ value,onChange }) => {
    const [Text, setText] = useState('');
    useEffect(() => {
        setText(value);
    }, [value]);
    
    const handleChange = (e)=>{
        const value = e.target.value;
        setText(value);
        onChange(value);
    }
    return (
        <div className='Textarea-root' >
            <textarea onChange={(e)=>handleChange(e)} value={Text} contentEditable={true} >
                {`${Text}`}
            </textarea>
        </div>
    )
}

export default TextareaWithLabel
