export function objToString(obj) {
    var str = '';
    for (var p in obj) {
        if (Object.prototype.hasOwnProperty.call(obj, p)) {
            str += p + '::' + obj[p] + '\n';
        }
    }
    return str;
}
export function propsToString(array) {
    var str = '';
    array.map((item, i) => {
        let lastChild = (array.length - 1 !== i) ? "," : ""

        str += item.name + lastChild;

    })
    return str;
}